<template>
    <section class="wrap" @click="on">
        <div v-if="show">{{piece}}</div>
        <div v-else></div>
    </section>
</template>
<script>
    export default {
        props: ["n"],
        data(){
            return{
                show: false,
                piece: ''
            }
        },
        methods:{
            on(){
                if(this.piece !== ''){return;}
                this.show = true;
                this.piece = this.n % 2 ? 'O' : 'X';
                this.$emit('click' , this.piece , this.show);
            }
        }
    }
</script>

<style scoped>
    .wrap{
        width: 100px;
        height: 100px;
        border: 1px solid;
        text-align: center;
        line-height: 100px;
        font-size: 40px;
        font-weight: 700;

        /* 设置鼠标不可选择 */
        -moz-user-select: none;
        -webkit-user-select: none;
        -ms-user-select: none;
        -khtml-user-select: none;
        user-select: none;
    }
</style>