<template>
    <section class="sec">
        <div class="hint">经典井字棋</div>
        <br>
        <section class="container">
            <cell @click="onClickCell(0 , arguments)" :n="n"></cell>
            <cell @click="onClickCell(1 , arguments)" :n="n"></cell>
            <cell @click="onClickCell(2 , arguments)" :n="n"></cell>
        </section>
        <section class="container">
            <cell @click="onClickCell(3 , arguments)" :n="n"></cell>
            <cell @click="onClickCell(4 , arguments)" :n="n"></cell>
            <cell @click="onClickCell(5 , arguments)" :n="n"></cell>
        </section>
        <section class="container">
            <cell @click="onClickCell(6 , arguments)" :n="n"></cell>
            <cell @click="onClickCell(7 , arguments)" :n="n"></cell>
            <cell @click="onClickCell(8 , arguments)" :n="n"></cell>
        </section>
        <br>
        <!-- <div class="hint">结果：{{result}}</div> -->
    </section>
</template>
<script>
    import cell from '@/components/Cell'

    export default {
        components:{
            cell
        },
        data(){
            return{
                n: 0,
                map: [
                    [null , null , null],
                    [null , null , null],
                    [null , null , null]
                ],
                // num: 1,
            }
        },
       
        methods:{
            onClickCell(i , args){
                this.n++;
                console.log(`第${i}个被点击,被点击的内容是${args[0]},show为${args[1]}`);
                const map = this.map;
               
                // i          map
                // 0          map[0][0]
                // 1          map[0][1]
                // 2          map[0][2]
                // 3          map[1][0]
                // 4          map[1][1]
                // 5          map[1][2]
                // 6          map[2][0]
                // 7          map[2][1]
                // 8          map[2][2] 
                map[Math.floor(i/3)][i%3] = args[0];  // 使对应的格子获得对应的棋子
                
                for (let i=0 ; i<=2 ; i++) {    // 其中一行都是一种棋子时
                  if(map[i][0] !== null && map[i][0] === map[i][1] && map[i][1] === map[i][2]) {
                    let timerID = setTimeout(() => {
                        alert(`游戏结束 , ${map[i][0]} 胜利`);
                        this.num++;
                    }, 100);
                  }
                }

                for (let j=0 ; j<=2 ; j++) {    // 其中一列都是一种棋子时
                  if(map[0][j] !== null && map[0][j] === map[1][j] && map[1][j] === map[2][j]) {
                    let timerID = setTimeout(() => {
                        alert(`游戏结束 , ${map[0][j]} 胜利`);
                        this.num++;
                    }, 100);
                  }
                }

                // 两种斜着的情况
                if(map[0][0] !== null && map[0][0] === map[1][1] && map[1][1] === map[2][2]) {
                    let timerID = setTimeout(() => {
                        alert(`游戏结束 , ${map[0][0]} 胜利`);
                        this.num++;
                    }, 100);
                }
                if(map[0][2] !== null && map[0][2] === map[1][1] && map[1][1] === map[2][0]) {
                    let timerID = setTimeout(() => {
                        alert(`游戏结束 , ${map[0][2]} 胜利`);
                        this.num++;
                    }, 100);
                }
            }
            
        }
    }

</script>

<style scoped>
.sec{
  margin-top: 100px;
}
.container{
    display: flex;
    justify-content: center;
}
.hint{
    text-align: center;
}
</style>